*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html, body {
  font-size: 62.5%;
  font-family: 'Raleway', sans-serif; }

.footer__useful-links {
  margin: 15rem auto 0 auto;
  font-size: 1.6rem;
  border-top: 1px solid rgba(156, 156, 156, 0.5);
  padding: 1rem 0;
  border-bottom: 1px solid rgba(156, 156, 156, 0.5); }
  .footer__useful-links ul {
    display: flex;
    justify-content: space-around;
    list-style: none;
    max-width: 100rem;
    margin: 0 auto; }
  .footer__useful-links :link,
  .footer__useful-links :visited {
    text-decoration: none;
    color: #A3A3A3;
    font-weight: 500; }

.footer__social-links {
  color: #9AB4B3;
  font-size: 1.6rem;
  display: flex;
  justify-content: space-around;
  max-width: 30rem;
  margin: 0 auto; }

.fab {
  border: 1px solid rgba(154, 180, 179, 0.5);
  border-radius: 10rem;
  padding: 1rem;
  margin: 4rem 0;
  cursor: pointer; }

.footer__copyright {
  text-align: center;
  color: #A3A3A3;
  font-weight: 400;
  letter-spacing: 0.2rem;
  font-size: 1.2rem;
  margin-bottom: 2rem; }
  .footer__copyright span:nth-child(1) {
    margin-bottom: 2rem;
    display: block; }
  .footer__copyright span:nth-child(2) {
    color: #2d2b2b;
    font-style: italic;
    font-weight: 400;
    display: block; }

.header {
  text-align: center;
  text-transform: uppercase;
  font-size: 1.6rem;
  letter-spacing: 0.2rem;
  border-bottom: 1px solid rgba(156, 156, 156, 0.5); }
  .header__primary {
    padding: 3.5rem 0;
    color: #2d2b2b;
    position: relative; }
    .header__primary h1 {
      font-weight: 400;
      display: inline-block; }

.header__primary-hamburger {
  display: none;
  position: absolute;
  top: 42%;
  right: 5%;
  padding: 2rem;
  cursor: pointer; }
  .header__primary-hamburger span:nth-child(1) {
    width: 4rem;
    content: '';
    position: absolute;
    top: 0.5rem;
    height: 0.2rem;
    background: black;
    transform: translateX(-50%); }
  .header__primary-hamburger span:nth-child(2) {
    width: 4rem;
    content: '';
    position: absolute;
    top: 1.5rem;
    height: 0.2rem;
    background: black;
    transform: translateX(-50%); }
  .header__primary-hamburger span:nth-child(3) {
    width: 4rem;
    content: '';
    position: absolute;
    top: 2.5rem;
    height: 0.2rem;
    background: black;
    transform: translateX(-50%); }

.header__secondary {
  max-width: 156rem;
  margin: 0 auto;
  padding-bottom: 2rem; }
  .header__secondary ul {
    display: flex;
    justify-content: space-around;
    list-style: none; }
  .header__secondary ul img {
    width: 2rem;
    height: 2rem;
    cursor: pointer; }
  .header__secondary :link,
  .header__secondary :visited {
    text-decoration: none;
    color: #2d2b2b;
    font-weight: 600; }

.billboard {
  display: flex;
  max-width: 145rem;
  margin: 0 auto; }
  .billboard__img {
    width: 71rem;
    height: 75rem;
    padding: 2.5rem 1rem 2.5rem 0rem; }
    .billboard__img img {
      width: 100%;
      height: 100%; }
  .billboard__action {
    width: 71rem;
    height: 75rem;
    padding: 2.5rem 0rem 2.5rem 1rem; }
    .billboard__action--bck {
      background: #9AB4B3;
      width: 100%;
      height: 100%;
      position: relative; }
      .billboard__action--bck-text {
        text-align: center;
        position: absolute;
        right: 0;
        left: 0;
        top: 30%;
        color: #fff; }
        .billboard__action--bck-text span:nth-child(1) {
          font-size: 1.8rem;
          font-weight: 300;
          font-style: italic;
          display: block; }
        .billboard__action--bck-text span:nth-child(2) {
          font-weight: 400;
          text-transform: uppercase;
          font-size: 8.6rem;
          display: inline-block;
          width: 60%;
          letter-spacing: 0.7rem; }
        .billboard__action--bck-text span:nth-child(3) {
          display: block;
          width: 10%;
          margin: 0 auto;
          border-bottom: 1px solid #fff;
          padding-top: 2.5rem; }

.favorites {
  max-width: 145rem;
  margin: 7rem auto;
  text-align: center; }
  .favorites__heading h2 {
    text-transform: uppercase;
    color: #2d2b2b;
    font-size: 3rem;
    letter-spacing: 0.7rem; }
  .favorites span {
    font-weight: 500;
    font-style: italic;
    font-size: 2rem;
    margin: 2rem 0 7rem;
    display: block;
    color: #A3A3A3;
    letter-spacing: 0.3rem; }
  .favorites__products {
    display: flex;
    justify-content: space-evenly; }
    .favorites__products-box {
      width: 34rem;
      height: 45rem;
      margin-top: 1rem;
      margin-right: 1rem; }
      .favorites__products-box span {
        font-size: 1.6rem;
        font-weight: 600;
        margin: 1.6rem 0;
        display: inline-block; }
      .favorites__products-box--price {
        font-size: 1.6rem;
        display: block; }
      .favorites__products-box--price del {
        color: #A3A3A3;
        font-weight: 600; }
      .favorites__products-box--price span {
        color: #99BCDF;
        margin-left: 1rem; }
      .favorites__products-box-img {
        width: 34rem;
        height: 37rem; }
      .favorites__products-box-img-one {
        background: url(img/monika.jpg);
        background-size: cover;
        background-position: center;
        height: 100%;
        width: 100%;
        position: relative; }
      .favorites__products-box-img-two {
        background: url(img/tanalee.jpg);
        background-size: cover;
        background-position: center;
        height: 100%;
        width: 100%;
        position: relative; }
      .favorites__products-box-img-three {
        background: url(img/alexandra.jpg);
        background-size: cover;
        background-position: center;
        height: 100%;
        width: 100%;
        position: relative; }
      .favorites__products-box-img-four {
        background: url(img/christie.jpg);
        background-size: cover;
        background-position: center;
        height: 100%;
        width: 100%;
        position: relative; }

.sales__tag {
  position: absolute;
  top: -1.8rem;
  text-align: center;
  left: 0;
  right: 0;
  border: 1px solid grey;
  display: inline-block;
  width: 2rem;
  height: 1.65rem;
  margin: 0 auto;
  padding: 1rem;
  border-radius: 10rem;
  background: black; }

.sales__tag > span {
  font-size: 1.2rem;
  color: #fff;
  font-weight: 300;
  font-style: italic;
  margin: 0;
  letter-spacing: 0; }

.services {
  max-width: 143rem;
  margin: 0 auto; }
  .services__images {
    display: flex;
    justify-content: space-between;
    padding: 0 0 2.5rem; }
    .services__images-one {
      background: url(img/thomas.jpg);
      position: relative;
      width: 45rem;
      height: 44rem;
      background-size: cover;
      margin-right: 1rem; }
    .services__images-two {
      background: url(img/beatriz-perez.jpg);
      position: relative;
      width: 45rem;
      height: 44rem;
      background-size: cover;
      margin-right: 1rem; }
    .services__images-three {
      background: url(img/tom.jpg);
      position: relative;
      width: 45rem;
      height: 44rem;
      background-size: cover;
      margin-right: 1rem; }
    .services__images-box {
      width: 95%;
      height: 30%;
      background: #fff;
      position: absolute;
      top: 65%;
      left: 3%;
      border: 1px solid rgba(156, 156, 156, 0.5); }
    .services__images-box-text {
      width: 97%;
      height: 92%;
      border: 1px solid rgba(156, 156, 156, 0.5);
      display: block;
      margin: 1% auto;
      text-align: center;
      font-size: 1.6rem;
      position: relative; }
      .services__images-box-text-span {
        top: 20%;
        position: absolute;
        text-align: center;
        left: 0;
        right: 0;
        letter-spacing: 0.3rem; }
      .services__images-box-text span {
        padding: 1rem 0;
        display: block;
        font-weight: 500; }
      .services__images-box-text span:nth-child(1) {
        color: #AEC2C1; }
      .services__images-box-text span:nth-child(2) {
        color: #9c9c9c; }

/* Media Queries */
@media (min-width: 320px) and (max-width: 780px) {
  html {
    font-size: 6px; }
  .header__primary-hamburger {
    display: inline-block; }
  .header__secondary {
    display: none;
    position: absolute;
    right: 0;
    padding: 0 5%;
    border: 1px solid rgba(156, 156, 156, 0.5);
    z-index: 1;
    background: #fff; }
  .header__secondary ul {
    display: block; }
  .header__secondary img {
    padding: 1.5rem; }
  .header__secondary :link,
  .header__secondary :visited {
    display: block;
    padding: 1.5rem; }
  .billboard__img {
    height: 40rem; }
  .billboard__action {
    height: 40rem; }
  .billboard__action--bck-text span:nth-child(2) {
    font-size: 3.6rem; }
  .services__images-box-text {
    font-size: 2.6rem;
    height: 88%; }
  .services__images {
    display: block;
    padding: 0 2.5rem 2.5rem; }
  .services__images-one {
    margin: 1.5rem auto;
    width: 100%;
    height: 60rem; }
  .services__images-two {
    margin: 1.5rem auto;
    width: 100%;
    height: 60rem; }
  .services__images-three {
    margin: 1.5rem auto;
    width: 100%;
    height: 60rem; } }
